$list-group-border-color: #d8e2ef;
$list-group-action-hover-color: #5e6e82;
$list-group-hover-bg: #f9fafd;
$table-group-separator-color: #bdbdbd;

$primary: #6088b3; //#6088b3//mau truoc do: #3699ff
$info: $cyan;
$secondary: #e4e6ef;
$success: #4dbd74;
$warning: #ffa800;
$danger: #f64e60;
$light: #f3f6f9;
$dark: #131628;

$theme-colors: (
  "primary": $primary,
  "info": $info,
  "secondary": $secondary,
  "success": $success,
  "warning": $warning,
  "danger": $danger,
  "light": $light,
  "dark": $dark,
);

:root {
  --color-primary: #{$primary};
  --color-info: #{$info};
  --color-secondary: #{$secondary};
  --color-success: #{$success};
  --color-warning: #{$warning};
  --color-danger: #{$danger};
  --color-light: #{$light};
  --color-dark: #{$dark};
  --color-text: #5e6e82;
  --color-bg-focus: #f7fbff;
  --color-placeholder: #c6c6c6;
}

@each $variant, $color in $theme-colors {
  .border-color-#{$variant} {
    border-color: $color !important;
  }
}

