// remove bg td
.table-hover > tbody > tr:hover > *{
  --bs-table-accent-bg: rgb(255 255 255 / 0%);
}

//remove default table-striped
.table-striped > tbody > tr:nth-of-type(odd) > *{
  --bs-table-accent-bg: none;
}

//bo sung striped khi co table expand
.table-has-expand>.react-bs-table>.react-bs-container-body>.table-striped>tbody>tr:nth-of-type(odd) {
  background-color: transparent;
}
.table-has-expand>.react-bs-table>.react-bs-container-body>.table-striped>tbody>tr:nth-of-type(4n-1) {
  background-color: rgba(0, 0, 0, 0.03);
}

/* Text */ 
.text-ellipsis-l3{
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.text-ellipsis-l2{
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}