/* animation */
.mui-lazy-loading {
  text-align: center;
  min-height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ani-fadeIn {
  animation: 1s aniFadeIn;
  animation-fill-mode: forwards;
  visibility: hidden;
}
/* An lazy loading neu 2 loading ke ben nhau */
.mui-lazy-loading +  .mui-lazy-loading{
  display: none;
}
@keyframes aniFadeIn {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}

form .a-footer {
  margin-top: 0.5rem;
}

.react-bs-container-body td > div.disabled {
  cursor: normal;
  /* background: #f0f3f5; */
  background: #fbfbfb;
  /* pointer-events: none; */
  /* touch-action: none; */
  opacity: 0.8;
}

.a-overlay {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #e9ecef99;
  opacity: 0.5;
}

.a-overlay-center{
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #e9ecef99;
  opacity: 0.5;
  display: flex;
  align-items: center;
  justify-content: center;
}

.a-flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.no-print-col, .no-print-col *
{
  /* visibility: collapse !important;
  display: none !important; */
}
@media print
{ 
  .no-print, .no-print *
  {
    display: none !important;
  }
  .no-print-col, .no-print-col *
  {
    visibility: collapse !important;
    display: none !important;
  }
  /* fix header when print */
  .fixed-sidebar:not(.fixed-header) .app-header{
    margin-left: 0px;
  }
  .app-header__logo, .app-header__logo>.logo-src{
    display: flex!important;
  }
  .app-header__logo>.app-sidebar-header, .app-header__logo>.header__pane{
    display: none!important;
  }
